<template>
    <div class="p-ab bg">
        <img src="../assets/img/2.png" style="width: 100%">
        <div class="layer">
            <h3 class="t-center yellow h31"></h3>
            <div class="font">A.</div>
            <div class="font">B.</div>
            <div class="font">C.</div>
            <h3 class="t-center yellow h32"></h3>
            <div class="font">A.5V5</div>
            <div class="font">B.</div>
            <div class="font">C.84BO142<br>BO3</div>
            <div class="font h31"><span class="yellow">5912:00-51418:00</span></div>
        </div>
        <img src="../assets/img/down.png" class="img-fix animated infinite flash" v-on:click="next">
    </div>
</template>
<style scoped>
    .bg {
        background: url("../assets/img/back2.jpg") no-repeat center;
        background-size: cover;
    }

    .font {
        font-size: 14px;
        margin-left: 50px;
        color: #ffffff;
    }
    .p-ab {
        position: relative;
        width: 100%;
        height: 100%;
    }

    .img-fix {
        position: fixed;
        left: 50%;
        margin-left: -21px;
        width: 42px;
        bottom: 20px;
    }
    .layer {
        margin: 40px auto 0 auto;
        border-radius: 5px;
        width: 92%;
        height: 45%;
        background-color: #20305F;
        opacity: 0.85;
    }

    .h31 {
        padding-top: 8px;
        margin-bottom: 8px;
    }

    .h32 {
        margin: 8px 0;
    }
</style>
<script>
    import util from '../util'
    export default {
        created(){
            /**
             * jssdk
             * 
             */
            antlinker.configTitle({
                type: "label",
                title: '',
                fail: function () {

                },
                success: function () {
                }
            });
            antlinker.configTitleButton({
                type: 'close',
                text: '',
                fail: function () {

                },
                success: function () {
                },
                trigger: function () {
                }

            });
        },
        methods: {
            next() {
                util.postJson({
                    Router: '/api/sport/check',
                    Method: 'GET'
                }).then(res => {
                    if (!res.Leader && !res.Mumber) {
                        this.$router.replace('iden');
                    } else if (res.Leader && !res.Mumber) {
                        this.$router.replace('success');

                    } else if (res.Mumber && !res.Leader) {
                        this.$router.replace('mdetail');

                    } else {
                        util.toast('')
                    }
                });
            }
        }
    }
</script>


