<template>
    <div class="p-ab bg">
        <div class="layer">
            <h3 class="t-center yellow pt"></h3>
            <div class="t-center zi">()</div>
            <input type="text" v-model="dz.Name" autofocus placeholder="">
            <input type="text" v-model="dz.Leader" autofocus placeholder="">
            <input type="text" v-model="dz.Phone" autofocus placeholder="">
            <button type="button" class="t-center block m-auto btn dy" v-on:click="next"></button>
        </div>
    </div>
</template>
<style scoped>
    .p-ab {
        position: relative;
        width: 100%;
        height: 100%;
    }

    .bg {
        background: url("../assets/img/back4.jpg") no-repeat;
        background-size: cover;
    }

    .layer {
        position: absolute;
        top: 50%;
        margin-top: -150px;
       /* margin: 200px auto 0 auto;*/
        border-radius: 5px;
        width: 92%;
        left: 4%;
        height: 300px;
        background-color: #20305F;
        opacity: 0.85;
    }

    .pt {
        padding-top: 16px;
        margin: 8px 0;
    }

    .zi {
        color: #ffffff;
        margin-bottom: 16px;
        font-size: 14px;
    }

    .btn {
        width: 140px;
        margin-top: 15px;
        border: 1px;
        font-size: 18px;
        height: 45px;
        border-radius: 20px;
        letter-spacing: 2px;
    }

    .dy {
        background-color: #F77A71;
    }
</style>
<script>
    import util from '../util'
    export default {
        data(){
            return {
                dz: {}
            }
        },
        created () {
            /**
             * jssdk
             * 
             */
            antlinker.configTitle({
                type: "label",
                title: '',
                fail: function () {

                },
                success: function () {
                }
            });
            antlinker.configTitleButton({
                type: 'close',
                text: '',
                fail: function () {

                },
                success: function () {
                },
                trigger: function () {
                }

            });
        },
        methods: {
            next(){
                var vm =this;
                if (!vm.dz.Leader) {
                    util.toast('');
                    return
                }
                if (!vm.dz.Name) {
                    util.toast('');
                    return
                }
                if (!vm.dz.Phone) {
                    util.toast('');
                    return
                }
                util.postJson({
                    Router: '/api/sport/team',
                    Method: 'PUT',
                    body: vm.dz
                }).then(res => {
                    if (res.code) {
                        util.toast(res.message);
                        // this.$router.push('rule');
                    } else {
                        this.$router.replace('success');
                    }
                });
            }
        }
    }
</script>


